.home-container {
    display: flex;
}

.home-main {
    flex: 1;
    position: relative;
    margin-left: 16rem;
    transition: margin-left 350ms cubic-bezier(0.165, 0.84, 0.44, 1) 0ms;
    display: flex;
    flex-direction: column;
    position: relative;
    height: 100vh;
}

.home-main.sidebar-close {
    margin-left: 0;
}

.main-content {
    height: 100%;
    padding-top: 10px;
    padding-left: 100px;
    padding-right: 100px;
}