.signup-container{
    max-width: 500px;
    margin: auto;
    margin-top: 100px;
}

.signup-header h1{
    text-align: center;
    font-weight: 400;
    font-size: 1.6rem;
    margin: 10px;
}


.signup-avatar{
    width: 40px;
    height: 40px;
    background-color: crimson;
    color: white;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: auto;
}

.group-link{
    display: flex;
    justify-content: space-between;
    color: crimson;
    margin-top: 15px;
    
}

.group-link a{
    font-size: 16px;
    color:#23ABFC;
}

.copy-right{
    display: flex;
    justify-content: center;
    margin-top: 100px;
}
.full-name{
    display: flex;
   
}
.full-name>div:first-child{
    margin-right: 10px;
}

.full-name>div:last-child{
    margin-left: 10px;
}